import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import IUser from '../../../types/auth/IMember';
import { Pagination } from '@mui/material';
import AdminService from '../../../services/auth/adminService';

function AdminMemberList({setSelectTab, setDataId}:{setSelectTab:any, setDataId:any}) {
  // todo:  
  //   
  const [dept, setDept] = useState<Array<IUser>>([]);
  //  
  const [search, setSearch] = useState<string>("");
  const [select, setSelect] = useState<string>("id");

  // todo:   : page(), count(), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [pageSize, setPageSize] = useState<number>(3); // 1
  // todo:  pageSizes :  (  )
  const pageSizes = [3, 6, 9];

  // todo:  
  // 1)  mounted    : useEffect(() => {},[])
  // 2)     : useEffect(() => {},[])
  useEffect(() => {
    retrieveDept(); //  
  }, [page, pageSize]);

  //    
  const retrieveDept = () => {
    //    : + (page), 1(pageSize)
    AdminService.getMemberAll(search,select,page-1,pageSize)
    .then((response:any)=>{
      console.log(response);
      const {totalPages, member} = response.data;
      setDept(member);
      setCount(totalPages);
    })
    .catch((e:Error)=>{console.log(e)})

  };

  //     
  const onChangeSearchDname = (e: any) => {
    setSearch(e.target.value);
  };

  // todo: handlePageSizeChange() : pageSize    
  //  select    :  ->  
  const handlePageSizeChange = (event: any) => { 
      setPageSize(event.target.value); // 1 (3,6,9)
      setPage(1); //  : 1 
   }

  //  todo: Pagination  ()
  //     => page   
  const handlePageChange = (event:any, value:number) => { 
      // value ==  
      setPage(value);
   }

   const onChangeSelect = (e:any) => {
    setSelect(e.target.value);
   }

  return (
    // 
    <>
      {/*  start */}
      {/*  end */}

      {/* dname start */}
      <div className="row mb-5 justify-content-center">
        {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
        <div className="col-8 w-50 input-group mb-3 mt-5">
          <select value={select} onChange={onChangeSelect}>
            <option value="id">ID</option>
            <option value="name"></option>
            <option value="ename">E.</option>
            <option value="email"></option>
          </select>
          <input
            type="text"
            className="form-control"
            placeholder="Search by dname"
            value={search}
            onChange={onChangeSearchDname}
          />
          <div className="input-group-append">
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={retrieveDept}
            >
              Search
            </button>
          </div>
        </div>
      </div>
      {/* dname end */}

      {/* paging  */}
      <div className="mt-3">
        {"Items per Page: "}
        <select onChange={handlePageSizeChange} value={pageSize}>
          {pageSizes.map((size) => (
            <option key={size} value={size}>
              {size}
            </option>
          ))}
        </select>

        {/*  : count={1} , page={} */}
        <Pagination
          className="my-3"
          count={count}
          page={page}
          siblingCount={1}
          boundaryCount={1}
          variant="outlined"
          shape="rounded"
          onChange={handlePageChange}
        />
      </div>
      {/* paging  */}

      {/* table start */}
      <div className="col-md-12">
        {/* table start */}
        <table className="table">
          <thead className="table-light">
            <tr>
              <th scope="col">Id</th>
              <th scope="col"></th>
              <th scope="col">E.</th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {dept &&
              dept.map((data, idx) => (
                <tr key={idx}>
                  <td>{data.memberId}</td>
                  <td>{data.memberName}</td>
                  <td>{data.memberEname}</td>
                  <td>{data.memberEmail}</td>
                  <td>{data.memberMile}</td>
                  <td>
                    <a href="#" onClick={()=>{setSelectTab(""); setDataId(data.memberId)}}>
                      <span className="badge bg-success">Edit</span>
                    </a>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
        {/* table end */}
      </div>
      {/* table end */}
    </>
  );
}

export default AdminMemberList