// components/common/Nav.tsx : rfce
import React, { useCallback } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { RootState, useAppDispatch } from "../../store/store";
import { logout } from "../../store/slices/auth";

function Nav() {

  const { user: currentUser } = useSelector((state:RootState)=> state.auth);

  const dispatch = useAppDispatch();

  //     : useCallback() : 
  const logOut = useCallback(() => { 
    dispatch(logout());
   },[dispatch]);
  

  return (
    <>
      {/* <!--   --> */}
      <nav className="site-nav">
        <div className="container">
          <div className="site-navigation">
            {/*   */}
            <ul
              className="js-clone-nav d-none d-lg-inline-block text-left site-menu"
            >
              <Link to="/" className="logo">
                <img src="/images/.png" className="main-logo-image" />
                GreanAir
              </Link>
              

              {/*    */}
              <li>
                <Link to="/reserve"></Link>
              </li>
              {/*    */}

              {/*     */}
              <li>
                <Link to="/search-reservation"> </Link>
              </li>
              {/*     */}

              {/*    */}
              <li>
                <Link to="/checkin"></Link>
              </li>
              {/*    */}

              {/*    */}
              <li className="has-children">
                <Link to="#"></Link>
                <ul className="dropdown">
                  <li>
                    <Link to="/elements"></Link>
                  </li>
                  <li>
                    <Link to="/elements">  </Link>
                  </li>
                  <li>
                    <Link to="/elements">1:1 </Link>
                  </li>
                  <li>
                    <Link to="/elements">1:1 </Link>
                  </li>
                </ul>
              </li>
              {/*    */}

              {/* <ul className="js-clone-nav d-none d-lg-inline-block site-menu"> */}
              {/* home   */}


              {/* <ul className="js-clone-nav d-none d-lg-inline-block site-menu"> */}
                {/* home   */}

                <ul className="login_menubar">
                  {!currentUser&&<>
                    <li>
                  <Link to="/login"></Link>
                </li>
                <li>
                  <Link to="/register">
                    <p></p></Link>
                </li>
                  </>
                }
                {currentUser&&<>
                    <li>
                  <Link to="#"onClick={logOut}></Link>
                </li>
                { currentUser.memberAuth ==="ROLE_USER" ?
                <li>
                  <Link to="/user-info">
                    <p></p></Link>
                </li> :
                <li>
                <Link to="/admin">
                  <p></p></Link>
              </li>
                }
                  </>
                }

                {/* home   */}
              </ul>
            </ul>
            {/* </ul> */}

            {/*   */}
            <a
              href="#"
              className="burger ml-auto float-right site-menu-toggle js-menu-toggle d-inline-block d-lg-none light"
              data-toggle="collapse"
              data-target="#main-navbar"
            >
              <span></span>
            </a>
          </div>
        </div>
      </nav>
      {/* <!--   --> */}
    </>
  );
}


export default Nav;
