import { useNavigate, useParams } from "react-router-dom";
import ReservationService from "../../services/reservation/ReservationService";
import React, { useEffect, useState } from "react";
import IReservation from "../../types/tour/IReservation";

function SeeReservation() {
  // todo:  
  //    (airlineReservationNumber)
  const { airlineReservationNumber } = useParams();

  //    
  let navigate = useNavigate();

  //  
  const initialReservation = {
    airlineReservationNumber: 0,
    adultCount: "",
    childCount: "",
    mileUseYn: "",
    memberYn: "", // 
    checkYn: "", //  
    memberId: "",
    userNumber: "", //  
    operationId: 0, //  ID
    airline: "",
    flightName: "", // 
    startAirport: "",
    finalAirport: "",
    startTime: "",
    finalTime: "",
    startDate: "",
    finalDate: "",
    domesticInternational: "",
    price: 0,
    memberName: "",
  };

  // reservation 
  const [reservation, setReservation] =
    useState<IReservation>(initialReservation);

  const getReservation = (airlineReservationNumber: any) => {
    ReservationService.get(airlineReservationNumber) //   
      .then((response: any) => {
        setReservation(response.data);
        console.log(reservation.airlineReservationNumber);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  useEffect(() => {
    if (airlineReservationNumber) getReservation(airlineReservationNumber);
  }, [airlineReservationNumber]);

  const backToSearchReservation = () => {
    navigate("/search-reservation");
  };

  return (
    <>
    
      <form className="form">
        <div className="blankBox3">&nbsp;</div>
        <div className="row mb-2">
          <h6 className="test1"> </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.airlineReservationNumber}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.adultCount}
            />
          </div>

          <h6 className="test1">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.childCount}
            />
          </div>

          <h6 className="test2"> </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.mileUseYn}
            />
          </div>

          <h6 className="test1"> ID&nbsp;&nbsp;&nbsp;</h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.memberId}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.airline}
            />
          </div>

          <h6 className="test1">&nbsp;</h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.flightName}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.startAirport}
            />
          </div>

          <h6 className="test1"> </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.finalAirport}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.startTime}
            />
          </div>

          <h6 className="test1"> </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.finalTime}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.startDate}
            />
          </div>

          <h6 className="test1"> </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.finalDate}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; - </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.domesticInternational}
            />
          </div>

          <h6 className="test1">&nbsp;</h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.memberName}
            />
          </div>

          <h6 className="test2">&nbsp;&nbsp;&nbsp;&nbsp; </h6>
          <div className="col-sm-12 col-md-6 mb-3 mb-lg-2 col-lg-4">
            <input
              name=""
              type="type"
              className="form-control"
              value={reservation.checkYn}
            />
          </div>

          <div className="btn1">
            <div className="col-sm-10">
              <input
                type="button"
                className="btn btn-primary btn-block"
                value=" "
                onClick={backToSearchReservation}
              />
            </div>
          </div>
        </div>
      </form>

      <div className="blankBox2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
    </>
  );
}

export default SeeReservation;
