import React, { useEffect, useState } from "react";
import "../../assets/css/login.css";
// todo:   lib
import { Form, ErrorMessage, Field, Formik } from "formik";
import * as Yup from "yup";
import { useAppDispatch } from "../../store/store";
import IUser from "../../types/auth/IMember";
import { register } from "../../store/slices/auth";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import DaumPostcode from "react-daum-postcode";
import initScripts from "../../assets/js/scripts";
import initCustom from "../../assets/js/custom";
import { Popover, OverlayTrigger, Button, Modal } from "react-bootstrap";

function Register() {
  // todo:  
  //    
  const [successful, setSuccessful] = useState<boolean>(false);
  //    
  const [message, setMessage] = useState<string>("");
  const [selectedDate, setSelectedDate] = useState<Date | null>(new Date());
  const [address, setAddress] = useState("");
  const [addApiView, setAddApiView] = useState(false);

  // todo:  (register) 
  const dispatch = useAppDispatch();

  //   : (Formik) 
  const initialValues = {
    memberId: "",
    memberPw: "",
    memberName: "",
    memberEname: "",
    memberEmail: "",
    memberPhone: "",
    memberSex: "male",
    memberAdd: "",
    memberAuth: "ROLE_USER",
    memberDate: "",
    memberCountry: "kor",
    memberMile: 0,
    repassword: "",
  };

  // todo:  
  //    : validationSchema(Formik & Yup )
  //    
  const validationSchema = Yup.object().shape({
    memberName: Yup.string()
      //      
      .test(
        "len",
        " 2 ~ 20  .",
        //   
        (val) => {
          if (
            val &&
            val.toString().length >= 3 &&
            val.toString().length <= 20
          ) {
            return true;
          }
          return false;
        }
      )
      .required(" ."), // username  
    memberId: Yup.string().test(
      "len",
      "id 4 ~ 20  .",
      //   
      (val) => {
        if (
          val &&
          val.toString().length >= 4 &&
          val.toString().length <= 20
        ) {
          return true;
        }
        return false;
      }
    ).required(" ."), // email  
    memberPw: Yup.string()
      //       
      .test(
        "len",
        "password 6 ~ 40   .", // 
        //   
        (val) => {
          if (
            val &&
            val.toString().length >= 6 &&
            val.toString().length <= 40
          ) {
            return true;
          }
          return false;
        }
      )
      .required(" ."), // password  
    repassword: Yup.string().oneOf(
      // repassword != password
      [Yup.ref("memberPw")],
      "  ."
    ),
    memberEmail: Yup.string().email("Invalid email address"),
    memberPhone: Yup.number().test(
      "phone",
      "  10~13 .",
      (val) => {
        if (val && val.toString().length >= 9 && val.toString().length <= 13) {
          return true;
        } else if (val == null) return true;
        return false;
      }
    ),
  });

  //    : Formik  onSubmit()  
  //  formValue : (email, password, username, repassword)
  const handleRegister = (formValue: any) => {
    //  
    const {
      memberId,
      memberPw,
      memberName,
      memberEname,
      memberEmail,
      memberPhone,
      memberSex,
      memberAdd,
      memberDate,
      memberCountry,
    } = formValue;

    //  
    const data: IUser = {
      // ) email:email => email (  )
      memberId,
      memberPw,
      memberName,
      memberEname,
      memberEmail,
      memberPhone,
      memberSex,
      memberAdd: address,
      memberAuth: "ROLE_USER",
      memberDate: selectedDate?.toISOString(),
      memberCountry: "kor",
      memberMile: 0,
      changePw:""
    };

    console.log(data);

    //   
    setSuccessful(false);
    // todo:    : (register)
    dispatch(register(data))
      .unwrap() //  
      .then(() => {
        setSuccessful(true);
        setMessage(" .");
      })
      .catch((e: Error) => {
        console.log(e);
        setSuccessful(false);
      });
  };


  const onChangeDate = (data: any) => {
    setSelectedDate(data);
  };

  const onCompleteAdd = (data: any) => {
    setAddress(data.address);
    setAddApiView(false);
  };

  const setPostCodeView = () => {
    setAddApiView(true);
  };

  useEffect(() => {
    initScripts();
    initCustom();
  }, []);

  return (
    // 
    <div>
      <div className="hero hero-inner">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6 mx-auto text-center">
              <div className="intro-wrap">
                <h1 className="mb-0"> </h1>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="card mt-5 container">
        <div className="card-body p-0">
          {/* <!-- Nested Row within Card Body --> */}
          <div className="row">
            <div className="col-lg-5 bg-register-image"></div>
            <div className="col-lg-7">
              <div className="p-5">
                <div className="text-center">
                  <h1 className="h4 text-gray-900 mb-4">Create an Account!</h1>
                </div>
                <Formik
                  initialValues={initialValues}
                  validationSchema={validationSchema}
                  onSubmit={handleRegister}
                >
                  {({ errors, touched }) => (
                    <Form className="user">
                      {!successful && (
                        <div>
                          {/*  */}
                          <div className="form-group">
                            <Field
                              type="text"
                              name="memberId"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberId && touched.memberId
                                  ? " is-invalid"
                                  : "")
                              }
                              id="exampleInputEmail"
                              placeholder="ID()"
                            />
                            <ErrorMessage
                              name="memberId"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>
                          {/*  */}
                          <div className="form-group row">
                            <div className="col-sm-6 mb-3 mb-sm-0">
                              <Field
                                type="password"
                                name="memberPw"
                                className={
                                  "form-control form-control-user mb-3" +
                                  (errors.memberPw && touched.memberPw
                                    ? " is-invalid"
                                    : "")
                                }
                                id="exampleInputPassword"
                                placeholder="Password()"
                              />
                              <ErrorMessage
                                name="memberPw"
                                component="div"
                                className="invalid-feedback"
                              />
                            </div>
                            <div className="col-sm-6">
                              <Field
                                type="password"
                                name="repassword"
                                className={
                                  "form-control form-control-user mb-3" +
                                  (errors.repassword && touched.repassword
                                    ? " is-invalid"
                                    : "")
                                }
                                id="exampleRepeatPassword"
                                placeholder="Repeat Password()"
                              />
                              <ErrorMessage
                                name="repassword"
                                component="div"
                                className="invalid-feedback"
                              />
                            </div>
                          </div>

                          {/*  */}
                          <div className="form-group">
                            <Field
                              type="text"
                              name="memberName"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberName && touched.memberName
                                  ? " is-invalid"
                                  : "")
                              }
                              id="exampleName"
                              placeholder="Full Name()"
                            />
                            <ErrorMessage
                              name="memberName"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>

                          {/*   */}
                          <div className="form-group">
                            <Field
                              type="text"
                              name="memberEname"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberEname && touched.memberEname
                                  ? " is-invalid"
                                  : "")
                              }
                              id="exampleName"
                              placeholder="English Name"
                            />
                            <ErrorMessage
                              name="memberEname"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>

                          {/* email */}
                          <div className="form-group">
                            <Field
                              type="email"
                              name="memberEmail"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberEmail && touched.memberEmail
                                  ? " is-invalid"
                                  : "")
                              }
                              id="exampleName"
                              placeholder="Email"
                            />
                            <ErrorMessage
                              name="memberEmail"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>

                          {/* ,  */}
                          <div className="form-group row">
                            <div className="col-sm-6 mb-3 mb-sm-0">
                              <Field
                                as="select"
                                name="memberSex"
                                className={
                                  "form-control form-control-select mb-3" +
                                  (errors.memberSex && touched.memberSex
                                    ? " is-invalid"
                                    : "")
                                }
                                id="userSexSelect"
                                placeholder=""
                              >
                                <option value="male" selected>
                                  
                                </option>
                                <option value="female"></option>
                              </Field>
                              <ErrorMessage
                                name="memberName"
                                component="div"
                                className="invalid-feedback"
                              />
                            </div>
                            <div className="col-sm-6">
                              <DatePicker
                                // showIcon
                                dateFormat="yyyy-MM-dd" //  
                                shouldCloseOnSelect //   datepicker  
                                minDate={new Date("2000-01-01")} // minDate    
                                maxDate={new Date()} // maxDate    
                                selected={selectedDate}
                                onChange={(data) => {
                                  onChangeDate(data);
                                }}
                                // customInput={<div></div>}
                                className="form-control form-control-select mb-3"
                              />
                            </div>
                          </div>

                          {/*  */}
                          <div className="form-group">
                            <div>
                              <input
                                type="text"
                                name="memberAdd"
                                className={
                                  "form-control form-control-user mb-3"
                                }
                                id="exampleName"
                                placeholder="Address"
                                onClick={setPostCodeView}
                                value={address}
                              />
                            </div>
                            <Modal
                              show={addApiView}
                              onHide={() => setAddApiView(false)}
                            >
                              <Modal.Body>
                                {" "}
                                <DaumPostcode
                                  style={{
                                    maxWidth: "400px",
                                    height: "500px",
                                    margin: "auto",
                                    display: addApiView ? "block" : "none",
                                  }}
                                  onComplete={(data) => {
                                    onCompleteAdd(data);
                                  }}
                                ></DaumPostcode>
                              </Modal.Body>
                              <Modal.Footer>
                                <Button
                                  className="btn"
                                  onClick={() => setAddApiView(false)}
                                >
                                  Close
                                </Button>
                              </Modal.Footer>
                            </Modal>
                          </div>

                          {/*  */}
                          <div className="form-group">
                            <Field
                              type="text"
                              name="memberPhone"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberPhone && touched.memberPhone
                                  ? " is-invalid"
                                  : "")
                              }
                              id="exampleName"
                              placeholder="Phone Number"
                            />
                            <ErrorMessage
                              name="memberPhone"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>

                          <button
                            type="submit"
                            className="btn btn-primary btn-user w-100 mb-3"
                          >
                            Register Account
                          </button>
                        </div>
                      )}

                      {successful && (
                        <div
                          className="alert alert-success text-center"
                          role="alert"
                        >
                          {message}
                        </div>
                      )}
                    </Form>
                  )}
                </Formik>

                <hr />
                <div className="text-center">
                  <a href="/forgot-password">Forgot Password?</a>
                </div>
                <div className="text-center">
                  <a href="/login">Already have an account? Login!</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;
