import React, { useEffect, useRef, useState } from 'react'
import { Nav } from 'react-bootstrap'
import AdminMemberList from '../admin/AdminMemberList';
import AdminMember from '../admin/AdminMember';
import OperationInfoManager from '../admin/OperationInfo/OperationInfoManager';
import AddOperationInfo from '../admin/OperationInfo/AddOperationInfo';
import OperationInfo from '../admin/OperationInfo/OperationInfo';
import { useSelector } from 'react-redux';
import { RootState } from '../../../store/store';
import { Link, useNavigate } from 'react-router-dom';
import BoardMain from '../admin/board/BoardMain';
import '../../../assets/css/jy/style_jy.css';
import AdminPayment from '../admin/AdminPayment';

function AdminPage() {

  const { user: currentUser } = useSelector((state:RootState)=> state.auth);

  const [selectTab, setSelectTab] = useState("");
  const [dataId, setDataId] = useState("");
  const navi = useNavigate();

  useEffect(()=>{
    if (currentUser?.memberAuth !="ROLE_ADMIN") navi("/");
  },[])

  const tabView = () => {
    if (selectTab==="") return <OperationInfoManager setSelectTab={setSelectTab} setDataId={setDataId}/>
    else if (selectTab==="") return <></>
    else if (selectTab==="") return <></>
    else if (selectTab==="") return <AdminMemberList setSelectTab={setSelectTab} setDataId={setDataId}/>
    else if (selectTab==="") return <AdminMember setSelectTab={setSelectTab} dataId={dataId}/>
    else if (selectTab===" ") return <AddOperationInfo setSelectTab={setSelectTab}/>
    else if (selectTab===" ") return <OperationInfo setSelectTab={setSelectTab} dataId={dataId}/>
    else if (selectTab==="") return <BoardMain/>
    else return <><AdminPayment/></>
  }

  return (
    <div>
       <div className="hero hero-inner">
        <div className="container">
          <div className=" align-items-center">
            <div className="col-lg-6 mx-auto text-center">
              <div className="intro-wrap">
                <h1 className="mb-0"></h1>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className='container'>

      <Nav fill variant="tabs" defaultActiveKey="link-0">
      <Nav.Item>

        <Nav.Link eventKey="link-0" onClick={()=>{setSelectTab("")}}> </Nav.Link>

      </Nav.Item>
      <Nav.Item>

        <Nav.Link eventKey="link-1" onClick={()=>{setSelectTab("")}}> </Nav.Link>

      </Nav.Item>
      <Nav.Item>

        <Nav.Link eventKey="link-2" onClick={()=>{setSelectTab("")}}> </Nav.Link>

      </Nav.Item>
      <Nav.Item>

        <Nav.Link eventKey="link-3" onClick={()=>{setSelectTab("")}}>
           
        </Nav.Link>
        </Nav.Item>
        <Nav.Item>
        <Nav.Link eventKey="link-4"  onClick={()=>{setSelectTab("")}}>
           
        </Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="link-5" onClick={()=>{setSelectTab("")}}>
           
        </Nav.Link>
      </Nav.Item>
    </Nav>
      {tabView()}
      </div>
    </div>
  )
}

export default AdminPage