import React, { useEffect, useState } from "react";
import "../../assets/css/login.css";
//   lib 
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState, useAppDispatch } from "../../store/store";
import IUser from "../../types/auth/IMember";
import { login } from "../../store/slices/auth";
import initScripts from "../../assets/js/scripts";
import initCustom from "../../assets/js/custom";
import DatePicker from "react-datepicker";
import { Button, Modal, Nav } from "react-bootstrap";
import Airport from "../../components/airport/Airport";
import Isotope from "isotope-layout";
import MyareaModal from "../modal/MyareaModal";

// 1)  
// 2)   lib  : Yup & Formik 
function Login() {

  useEffect(()=>{
    initScripts();
    initCustom();
  },[])



  // todo:  
  //    
  let navigate = useNavigate();

  // todo:  (state.) 
  // todo:  : useSelector((state)=> state.)
  //   (true/false)
  const { isLoggedIn } = useSelector((state: RootState)=> state.auth);
  const [tag, setTag] = useState("login");
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [departureAirPort, setDepartureAirPort] = useState("");
  const [arrivalAirPort, setArrivalAirPort] = useState("");
  const [isModal, setIsModal] = useState(false);
  const [isAirPort, setIsAirPort] = useState("");
  const [selectAirport, setSelectAirport] = useState({cname:"", abbr:""})

  const tagSelect = (data:string) => {
    if (data==="login") setTag("login");
    if (data==="non-member") setTag("non-member");
  }

  const setAirport = (data:any) => {
    if (isAirPort==="departure")
    setDepartureAirPort(data);
  if (isAirPort==="arrival")
    setArrivalAirPort(data);
  setIsModal(false);

  }




  // todo:   
  // todo:  : useAppDispatch()   : dispatch()
  // todo:  ) dispatch(login), dispatch(logout) 
  const dispatch = useAppDispatch();

  // todo:   lib 
  // todo: Formik  (initialValues) : html  
  // todo:   (email, password : input(==Field))
  const initialValues = {
    memberId:"",
    memberPw:"",
    memberName:"",
    memberEname:"",       
    memberEmail:"",       
    memberPhone:"",      
    memberSex:"",       
    memberAdd:"",       
    memberAuth:"ROLE_USER",       
    memberDate:"",       
    memberCountry:"",       
    memberMile:0,
  }

  const initNonMember = {
      domesticInternational:"",
      airlineReservationNumber:"",
      memberEname:"",
      departure: selectedDate,       
      departureAirPort:"",       
      arrivalAirPort:"",      
  }

  // todo:  
  // todo:   lib (Formit lib : validationSchema )
  // todo: validationSchema :    
  // todo:  : validationSchema = Yup.object().shape({})
  const validationSchema = Yup.object().shape({
    // email   : required() => 
    // string() :  ? 
    memberId: Yup.string().required(" ."),
    // password   : required() => 
    memberPw: Yup.string().required(" ."),
  });

  const validationSchemaNonMember = Yup.object().shape({
    // email   : required() => 
    // string() :  ? 
    airlineReservationNumber: Yup.string().required(" ."),
    // password   : required() => 
    memberEname: Yup.string().required(" .")
  });

  // isLoggedIn = true ( (true/false))
  //  /home 
  if(isLoggedIn) {
    navigate("/"); //   
  }

  //      : submit(Formit)
  // Formit lib   email, password  
  const handleLogin = (formValue: any) => { 
    // Formit  email, password  
      const { memberId,
        memberPw,
        memberName,
        memberEname,       
        memberEmail,       
        memberPhone,      
        memberSex,       
        memberAdd,
        memberDate,       
        memberCountry,  } = formValue;

    //   
    const data: IUser = {
      // ) email:email => email (  )
      memberId,
      memberPw,
      memberName,
      memberEname,       
      memberEmail,       
      memberPhone,      
      memberSex,       
      memberAdd,       
      memberAuth:"ROLE_USER",       
      memberDate,       
      memberCountry,       
      memberMile:0,  
      changePw:""   
    }

    // todo:   (  ) 
    dispatch(login(data))
      .unwrap()    //      
      .then(()=>{
        // alert(" .");
        //  /home  
        navigate("/");
        // js :  
        // window.location.reload();
      })
      .catch((e:Error)=>{
        console.log(e);
      });
   }

   const handleNonMember = (formValue: any) => { 
    formValue.arrivalAirPort = arrivalAirPort
    formValue.departureAirPort = departureAirPort
    console.log(formValue) 

  }

   const onChangeDate = (data:Date) => { setSelectedDate(data) }

   const innerHtml = () => {
    if (tag==="login") {
    return (<div className="p-5">
    {/* validationSchema : Formik    */}
    <Formik                          //    Formik lib                        
      initialValues={initialValues}  // ( :email,password) 
      validationSchema={validationSchema}  //   
      onSubmit={handleLogin}               //  ( )
    >
      {/* Form  : form   */}
      {/* Field : input   */}
      {/* errors. :     */}
      {/* touched. :    (true/false) */}
      {({ errors, touched }) => (
        <Form className="user">
          <div className="form-group">
            <Field
              type="text"
              name="memberId"
              className={
                "form-control form-control-user mb-3" +
                // email        
                (errors.memberId && touched.memberId
                  ? " is-invalid"
                  : "")
              }
              placeholder="Enter Email Address..."
            />
            {/* ErrorMessage :  email    */}
            <ErrorMessage
              name="memberId"
              component="div"
              className="invalid-feedback"
            />
          </div>
          {/*    */}
          <div className="form-group">
            <Field
              type="password"
              name="memberPw"
              className={
                "form-control form-control-user mb-3" +
                // password        
                (errors.memberPw && touched.memberPw
                  ? " is-invalid"
                  : "")
              }
              id="exampleInputPassword"
              placeholder="Password"
            />
            {/* ErrorMessage :  password    */}
            <ErrorMessage
              name="memberPw"
              component="div"
              className="invalid-feedback"
            />
          </div>

          <button
            type="submit"
            className="btn btn-primary btn-user w-100 mb-3"
          >
            Login
          </button>
          <hr />
          <a
            href={`https://accounts.google.com/o/oauth2/v2/auth?client_id=${process.env.REACT_APP_GOOGLE_REST_API_KEY}&redirect_uri=${"http://localhost:3000/login/auth/google"}&response_type=code&scope=email`}
            className="btn btn-google btn-user w-100 mb-2"
          >
            <i className="fab fa-google fa-fw"></i>&nbsp;Login
            with Google
          </a>
          <a
            href={`https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=${process.env.REACT_APP_NAVER_REST_API_KEY}&redirect_uri=${process.env.REACT_APP_NAVER_REDIRECT_URI}&state=${process.env.REACT_APP_STATE}`}
            className="btn btn-naver btn-user w-100 mb-2"
          >
            <i className="fa-solid fa-n"></i>&nbsp;Login with
            Naver
          </a>
          <a
            href={`https://kauth.kakao.com/oauth/authorize?response_type=code&client_id=${process.env.REACT_APP_KAKAO_REST_API_KEY}&redirect_uri=${process.env.REACT_APP_KAKAO_REDIRECT_URI}`}
            className="btn btn-kakao btn-user w-100 mb-3"
          >
            <i className="fa-solid fa-k"></i>&nbsp;Login with
            Kakao
          </a>
        </Form>
      )}
    </Formik>
    <hr />
    <div className="text-center">
      <a className="small" href="/forgot-password">
        Forgot Password?
      </a>
    </div>
    <div className="text-center">
      <a className="small" href="/register">
        Create an Account!
      </a>
    </div>
  </div>)}
    if (tag==="non-member") {
     return (<Formik                          //    Formik lib                        
      initialValues={initNonMember}  // ( :email,password) 
      validationSchema={validationSchemaNonMember}  //   
      onSubmit={handleNonMember}               //  ( )
    >
      {/* Form  : form   */}
      {/* Field : input   */}
      {/* errors. :     */}
      {/* touched. :    (true/false) */}
      {({ errors, touched }) => (
        <Form className="user col-11 mx-auto">
          <br></br><br></br>
          <div className="form-group">
            <Field
              type="text"
              name="airlineReservationNumber"
              className={
                "form-control form-control-user mb-3" +
                // email        
                (errors.airlineReservationNumber && touched.airlineReservationNumber
                  ? " is-invalid"
                  : "")
              }
              placeholder=" "
            />
          </div>
          <div className="row">
          <div className="form-group col-4">
          <DatePicker
                                showIcon
                                dateFormat="yyyy-MM-dd" //  
                                shouldCloseOnSelect //   datepicker  
                                minDate={new Date("2000-01-01")} // minDate    
                                maxDate={new Date()} // maxDate    
                                selected={selectedDate}
                                onChange={(data) => {
                                  if (data)
                                  onChangeDate(data);
                                }}
                                // customInput={<div></div>}
                                className="form-control form-control-select mb-3"
                              />
          </div>
          <div className="form-group col-4">
            <input
              type="text"
              name="departureAirPort"
              className={
                "form-control form-control-user mb-3" 
              }
              onClick={()=>{setIsModal(true); setIsAirPort("departure");}}
              id="departureAirPort"
              placeholder=" "
              value={departureAirPort}
            />
          </div>
          <div className="form-group col-4">
            <input
              type="text"
              name="arrivalAirPort"
              className={
                "form-control form-control-user mb-3"
                }
              onClick={()=>{setIsModal(true); setIsAirPort("arrival");}}
              id="arrivalAirPort"
              placeholder=" "
              value={arrivalAirPort}
            />

          </div>
          </div>
          {/*    */}
          <div className="row">
            

          <div className="form-group col-3">
            <Field
              as="select"
              name="domesticInternational"
              className={
                "form-control form-control-select mb-3" +
                // password        
                (errors.domesticInternational && touched.domesticInternational
                  ? " is-invalid"
                  : "")
              }
              id="exampleInputPassword"
              placeholder="">
                <option value={""}></option>
                <option value={""}></option>
              </Field>
            
          </div>
          <div className="form-group col-9">
            <Field
              type="text"
              name="memberEname"
              className={
                "form-control form-control-user mb-3" +
                // password        
                (errors.memberEname && touched.memberEname
                  ? " is-invalid"
                  : "")
              }
              id="memberEname"
              placeholder="   "
            />
          </div>
          </div>
          <button
            type="submit"
            className="btn btn-primary btn-user w-100 mb-3"
          >
             
          </button>
          <hr />
        </Form>
      )}
    </Formik>)
    }
   }


  return (
    
    // 
    <div>
            <div className="hero hero-inner">
        <div className="container">
          <div className=" align-items-center">
            <div className="col-lg-6 mx-auto text-center">
              <div className="intro-wrap">
                <h1 className="mb-0"></h1>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-lg-6">
          <div className="card mt-5">
            <div className="card-body p-0">
              {/* <!-- Nested Row within Card Body --> */}
              <div className="">
                <div className="">
                  <div>
<Nav fill variant="tabs" defaultActiveKey="link-0">
      <Nav.Item>

        <Nav.Link eventKey="link-0" onClick={()=>{tagSelect("login")}}><h3 className="container text-center" ></h3></Nav.Link>

      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="link-1" onClick={()=>{tagSelect("non-member")}}><h3 className="container text-center" > </h3></Nav.Link>

      </Nav.Item>
    </Nav>
                  </div>
                  <div className="col-11 mx-auto">

                  {innerHtml()}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div>
            {/* <Modal
            size="lg"
            show={isModal}
            onHide={() => setIsModal(false)}
            >
              <Modal.Header>
              <h3 className="tit"> </h3>
              </Modal.Header>
            <Modal.Body>
              {isAirPort === "arrival" && <><Airport setAirport={setAirport}></Airport></>}
              {isAirPort === "departure" && <><Airport setAirport={setAirport}></Airport></>}
            </Modal.Body>
                              <Modal.Footer>
                                <Button
                                  className="btn"
                                  onClick={() => setIsModal(false)}
                                >
                                  Close
                                </Button>
                              </Modal.Footer>
            </Modal> */}

<MyareaModal
            show={isModal}
            onHide={() => setIsModal(false)}
            onAbbrSelect={setAirport}
          />
            </div>
    </div>    
  );
}

export default Login;
