import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import IUser from "../../types/auth/IMember";
import authService from "../../services/auth/authService";
import IAuth from "../../types/auth/IAuth";
// todo:    : , 

//  user     :  () ->  
// user :  
//  : JSON.parse() =>  ->  
const user = JSON.parse(localStorage.getItem("user")||"null");

// todo ------------   ------------

// todo:   () 
//  : createAsyncThunk(, (,thunkAPI)=>{}) -    
//  thunkAPI  :   -> thunkAPI.rejectWithValue()
// async () => { await  }; 
// 1)      : promise -> .then().catch()
// 2)      :  async () => { await }
export const register = createAsyncThunk(
  "auth/register",
  async (user:IUser, thunkAPI) => {
    try {
      const response = await authService.register(user); //  
      return response.data; //  
    } catch (error: any) {
      return thunkAPI.rejectWithValue("register   .");
    }
  }
);
// todo:  ()
export const login = createAsyncThunk(
  "auth/login",                        // 
  async (user:IUser, thunkAPI) => {    // 
    try {
      const data = await authService.login(user); //  login 
      return { user: data }; //  
    } catch (error: any) {
      return thunkAPI.rejectWithValue("login   .");
    }
  }
);

export const kakaologin = createAsyncThunk(
  "auth/kakaologin",                        // 
  async (code:string, thunkAPI) => {    // 
    try {
      const data = await authService.kakaoLogin(code); //  login 
      return { user: data }; //  
    } catch (error: any) {
      return thunkAPI.rejectWithValue("login   .");
    }
  }
);

export const naverlogin = createAsyncThunk(
  "auth/naverlogin",                        // 
  async (code:string, thunkAPI) => {    // 
    try {
      const data = await authService.naverLogin(code); //  login 
      return { user: data }; //  
    } catch (error: any) {
      return thunkAPI.rejectWithValue("login   .");
    }
  }
);

export const googlelogin = createAsyncThunk(
  "auth/googlelogin",                        // 
  async (code:string, thunkAPI) => {    // 
    try {
      const data = await authService.googleLogin(code); //  login 
      return { user: data }; //  
    } catch (error: any) {
      return thunkAPI.rejectWithValue("login   .");
    }
  }
);

// todo:  ()
export const logout = createAsyncThunk(
  "auth/logout"
  , async () => {
  await authService.logout(); //   ( )
});

// todo ------------   ------------
// todo :    : { isLoggedIn: true/false, user: user/null }
// todo : user <=  
// todo : user  () initialState = { isLoggedIn: true, user: user }
// todo : user  () initialState = { isLoggedIn: false, user: null }
const initialState: IAuth = user
  ? { isLoggedIn: true, user }
  : { isLoggedIn: false, user: null };

// todo:   ( ) :   
const authSlice = createSlice({
  name: "auth",                  //   : auth
  initialState,                  //  3  
  reducers: {},                  //    
  // todo :  builder.addCase(.fulfilled, ) :     
  // todo :  builder.addCase(.rejected, )  :     
  extraReducers: (builder) => {  //    : register, login, logout     
    builder
    // register()  -> isLoggedIn = false 
      .addCase(register.fulfilled, (state) => {
        state.isLoggedIn = false;
      })
    // register()  -> isLoggedIn = false 
      .addCase(register.rejected, (state) => {
        state.isLoggedIn = false;
      })
    // login()  -> (state.)isLoggedIn = true 
    // login()  -> (state.)user = (action.payload.)user  
      .addCase(login.fulfilled, (state, action) => {
        state.isLoggedIn = true;
        state.user = action.payload.user;
      })
    // login()  -> (state.)isLoggedIn = false 
    // login()  -> (state.)user = null 
      .addCase(login.rejected, (state) => {
        state.isLoggedIn = false;
        state.user = null;
      })
      .addCase(kakaologin.fulfilled, (state, action) => {
        state.isLoggedIn = true;
        state.user = action.payload.user;
      })
    // login()  -> (state.)isLoggedIn = false 
    // login()  -> (state.)user = null 
      .addCase(kakaologin.rejected, (state) => {
        state.isLoggedIn = false;
        state.user = null;
      })
          // login()  -> (state.)isLoggedIn = true 
    // login()  -> (state.)user = (action.payload.)user  
    .addCase(naverlogin.fulfilled, (state, action) => {
      state.isLoggedIn = true;
      state.user = action.payload.user;
    })
  // login()  -> (state.)isLoggedIn = false 
  // login()  -> (state.)user = null 
    .addCase(naverlogin.rejected, (state) => {
      state.isLoggedIn = false;
      state.user = null;
    })
        // login()  -> (state.)isLoggedIn = true 
    // login()  -> (state.)user = (action.payload.)user  
    .addCase(googlelogin.fulfilled, (state, action) => {
      state.isLoggedIn = true;
      state.user = action.payload.user;
    })
  // login()  -> (state.)isLoggedIn = false 
  // login()  -> (state.)user = null 
    .addCase(googlelogin.rejected, (state) => {
      state.isLoggedIn = false;
      state.user = null;
    })
    // logout()  -> (state.)isLoggedIn = false 
    // logout()  -> (state.)user = null 
      .addCase(logout.fulfilled, (state) => {
        state.isLoggedIn = false;
        state.user = null;
      });
  },
});

export default authSlice.reducer;
