import React, { useEffect, useState } from 'react'
import IUser from '../../../../types/auth/IMember';
import { Pagination } from '@mui/material';
import IOperationinfo from '../../../../types/IOperationinfo';
import AdminService from '../../../../services/auth/adminService';
import { Link } from 'react-router-dom';

function OperationInfoManager({setSelectTab, setDataId}:{setSelectTab:any, setDataId:any}) {
  //   
  const [dept, setDept] = useState<Array<IOperationinfo>>([]);
  //  
  const [search, setSearch] = useState<string>("");
  const [select, setSelect] = useState<string>("operationId");

  // todo:   : page(), count(), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [pageSize, setPageSize] = useState<number>(3); // 1
  // todo:  pageSizes :  (  )
  const pageSizes = [3, 6, 9];

  // todo:  
  // 1)  mounted    : useEffect(() => {},[])
  // 2)     : useEffect(() => {},[])
  useEffect(() => {
    retrieveDept(); //  
  }, [page, pageSize]);

  //    
  const retrieveDept = () => {
    //    : + (page), 1(pageSize)
    AdminService.getOperationAll(search, select, page-1, pageSize)
    .then((response:any)=>{console.log(response)
        const {data, totalPages} = response.data;
    setDept(data);
    setCount(totalPages);
    // setPage(1);
    })
    .catch((e:Error)=>{console.log(e)})

  };

  //     
  const onChangeSearchDname = (e: any) => {
    setSearch(e.target.value);
  };

  // todo: handlePageSizeChange() : pageSize    
  //  select    :  ->  
  const handlePageSizeChange = (event: any) => { 
      setPageSize(event.target.value); // 1 (3,6,9)
      setPage(1); //  : 1 
   }

  //  todo: Pagination  ()
  //     => page   
  const handlePageChange = (event:any, value:number) => { 
      // value ==  
      setPage(value);
   }

   const onChangeSelect = (e:any) => {
    setSelect(e.target.value);
   }

  return (
    // 
    <>
      {/*  start */}
      {/*  end */}

      {/* dname start */}
      <div className="row mb-5 justify-content-center">
        {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
        <div className="col-8 w-50 input-group mb-3 mt-5">
          <select value={select} onChange={onChangeSelect} className='me-3'>
            <option value="operationId">ID</option>
            <option value="startAirport"></option>
            <option value="finalAirport"></option>
            <option value="airline"></option>
            <option value="flightName"></option>
          </select>
          <input
            type="text"
            className="form-control"
            placeholder="Search by dname"
            value={search}
            onChange={onChangeSearchDname}
          />
          <div className="input-group-append">
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={()=> {retrieveDept(); setPage(1)}}
            >
              Search
            </button>
            <div>
            
            <button className="btn btn-outline-secondary ms-3" onClick={()=>{setSelectTab(" ")}}></button>
            </div>
          </div>
        </div>
      </div>
      {/* dname end */}

      {/* paging  */}
      <div className="mt-3 " >
        {"Items per Page: "}
        <select onChange={handlePageSizeChange} value={pageSize}>
          {pageSizes.map((size) => (
            <option key={size} value={size}>
              {size}
            </option>
          ))}
        </select>

        {/*  : count={1} , page={} */}
        <div className=''>

        <Pagination
          className="my-3 col-6"
          count={count}
          page={page}
          siblingCount={1}
          boundaryCount={1}
          variant="outlined"
          shape="rounded"
          onChange={handlePageChange}
          
          />
            
          </div>
      </div>
      
      {/* paging  */}

      {/* table start */}
      <div className="col-md-12">
        {/* table start */}
        <table className="table">
          <thead className="table-light">
            <tr>
              <th scope="col">Id</th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {dept &&
              dept.map((data, idx) => {
                const startDate = data.startDate.toString().split("T");
                const finalDate = data.finalDate.toString().split("T");
                return (<tr key={idx}>
                  <td>{data.operationId}</td>
                  <td>{data.airline}</td>
                  <td>{data.flightName}</td>
                  <td>{data.startTime}</td>
                  <td>{data.finalTime}</td>
                  <td>{data.startAirport}</td>
                  <td>{data.finalAirport}</td>
                  <td>{startDate[0]}</td>
                  <td>{finalDate[0]}</td>
                  <td>{data.price}</td>
                  <td>{data.domesticInternational}</td>
                  <td>
                    <Link to="#" onClick={()=>{setSelectTab(" "); setDataId(data.operationId)}}>
                      <span className="badge bg-success">Edit</span>
                    </Link> 
                  </td>
                </tr>)
})}
          </tbody>
        </table>
        {/* table end */}
      </div>
      {/* table end */}
    </>
  )
}

export default OperationInfoManager