// authService.ts : axios    (register, login ,logout)
// http      (  )
import IUser from "../../types/auth/IMember"
import http from "../../utils/http-common"
import authHeader from "./authHeader";

// (register, signup) (insert)
const register = (user: IUser) => { 
    return http.post("/auth/signup", user);
 }

//   :   
const logout = () => { 
    localStorage.removeItem("user");
 }

 const getUserInfo = (userId:string) => {
    return http.get("/auth/info/"+userId);
 }

// todo:  (login, signin) : (post) why? 
// get ->       
// post ->     
const login = (user:IUser) => { 
    return http.post("/auth/signin", user) //   
    .then((response: any)=>{
        //  :  (accessToken)  
        //  :  +  +  
        if(response.data.accessToken) {
            //  ( -> ) 
            localStorage.setItem("user", JSON.stringify(response.data));
        }
        return response.data; //  
    });
 }

 const kakaoLogin = (code:string) => { 
    return http.post("/oauth/kakao", code) //   
    .then((response: any)=>{
        //  :  (accessToken)  
        //  :  +  +  
        if(response.data.accessToken) {
            //  ( -> ) 
            localStorage.setItem("user", JSON.stringify(response.data));
        }
        return response.data; //  
    });
 }

 const naverLogin = (code:string) => { 
    return http.post("/oauth/naver/"+code) //   
    .then((response: any)=>{
        //  :  (accessToken)  
        //  :  +  +  
        if(response.data.accessToken) {
            //  ( -> ) 
            localStorage.setItem("user", JSON.stringify(response.data));
        }
        return response.data; //  
    });
 }

 const googleLogin = (code:string) => { 
    return http.post("/oauth/google/"+code) //   
    .then((response: any)=>{
        //  :  (accessToken)  
        //  :  +  +  
        if(response.data.accessToken) {
            //  ( -> ) 
            localStorage.setItem("user", JSON.stringify(response.data));
        }
        return response.data; //  
    }).catch((e:Error)=>{console.log(e)});
 }

 const editUser = (user:IUser) => {
    return http.put("/auth/info",user, {headers : authHeader()});
 }

 const changePw = (data:any) => {
    return http.put("/auth/pchange", data, {headers : authHeader()});
 }

 const AuthService = {
    register,
    login,
    logout,
    kakaoLogin,
    naverLogin,
    googleLogin,
    getUserInfo,
    editUser,
    changePw
 }
 export default AuthService;