import React, { useState } from 'react'
import { useSelector } from 'react-redux';
import { RootState } from '../../../store/store';
import * as Yup from "yup";
import { ErrorMessage, Field, Form, Formik } from 'formik';
import IUser from '../../../types/auth/IMember';
import IAuth from '../../../types/auth/IAuth';
import AuthService from '../../../services/auth/authService';

function PasswordChage() {

    const { user: currentUser } = useSelector((state:RootState)=> state.auth);

    const [successful, setSuccessful] = useState<boolean>(false);
    const [message, setMessage] = useState<string>("");

    const initValue = {
        memberId: currentUser?.memberId,
        memberPw:"",
        repassword:"",
        changePw:"",
    }

    const validationSchema = Yup.object().shape({
        memberPw: Yup.string()
          //       
          .test(
            "len",
            "password 6 ~ 40   .", // 
            //   
            (val) => {
              if (
                val &&
                val.toString().length >= 6 &&
                val.toString().length <= 40
              ) {
                return true;
              }
              return false;
            }
          )
          .required(" ."), // password  
          changePw: Yup.string()
          //       
          .test(
            "len",
            "password 6 ~ 40   .", // 
            //   
            (val) => {
              if (
                val &&
                val.toString().length >= 6 &&
                val.toString().length <= 40
              ) {
                return true;
              }
              return false;
            }
          )
          .required(" ."), // password  
        repassword: Yup.string().oneOf(
          // repassword != password
          [Yup.ref("changePw")],
          "  ."
        ),

      });

      const handleChage = (formValue: any) => {
        // //  
        const {
          memberId,
          memberPw,
          changePw,
        } = formValue;
    
        // //  
        const data = {
          // ) email:email => email (  )
          memberId,
          memberPw,
          changePw,
        };
    
        setSuccessful(false);
        AuthService.changePw(data)
        .then((response:any)=>{console.log(response);
        setSuccessful(true);
    setMessage(".")})
        .catch((e:Error)=>{console.log(e)})
    
        //   
      };

  return (
    <div className='mt-5 col-6 container'>
                        <Formik
                  initialValues={initValue}
                  validationSchema={validationSchema}
                  onSubmit={handleChage}
                >
                  {({ errors, touched }) => (
                    <Form className="user">
                      {!successful && (
                        <div>
                          {/*  */}

                          <div className="form-group">
                             
                            <Field
                              type="text"
                              name="memberPw"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberPw && touched.memberPw
                                  ? " is-invalid"
                                  : "")
                              }
                              id="memberPw"
                              placeholder=" "
                            />
                            <ErrorMessage
                              name="memberPw"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>
                          <div className="form-group">
                             
                            <Field
                              type="text"
                              name="changePw"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.memberPw && touched.memberPw
                                  ? " is-invalid"
                                  : "")
                              }
                              id="changePw"
                              placeholder=" "
                            />
                            <ErrorMessage
                              name="changePw"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>
                          <div className="form-group">
                            
                            <Field
                              type="text"
                              name="repassword"
                              className={
                                "form-control form-control-user mb-3" +
                                (errors.repassword && touched.repassword
                                  ? " is-invalid"
                                  : "")
                              }
                              id="repassword"
                              placeholder="  "
                            />
                            <ErrorMessage
                              name="repassword"
                              component="div"
                              className="invalid-feedback"
                            />
                          </div>
                          <button
                            type="submit"
                            className="btn btn-primary btn-user w-100 mb-3"
                          >
                             
                          </button>
                        </div>
                      )}

                      {successful && (
                        <div
                          className="alert alert-success text-center"
                          role="alert"
                        >
                          {message}
                        </div>
                      )}
                    </Form>
                  )}
                </Formik>
    </div>
  )
}

export default PasswordChage
